:root{
  --primariycolor: #d81e5f;
  --secondarycolor: #8x3aff;
  --darkcolor: #131a26;
  --lightcolor: #22243e;

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Fira Sans", "sans-serif";

}
body{
  background-color: var(--lightcolor);
}
.App{
  display: flex;
  padding: 16px;
  min-height: 100vh;
  align-items: center;
  justify-content: center;

}
.calculator{
  width: 100%;
  max-width: 400px;
  background-color: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);

}
.display{
  padding: 16px;
  text-align: right;
  background-color: var(--darkcolor);
  color: #eee;
  font-size: 24px;
  font-weight: 300;
}
.display span{
  font-size: 14px;
  color: #888;
}
button{
  appearance: none;
  border: none;
  outline: none;
  color: #FFF;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;

}
button:hover{
  opacity: 0.9;
}
.operators{
  display: flex;

}
.operators button{
  flex: 1 1 0%;
  background-color: var(--primariycolor);
  font-weight: 700;
}
.numbers{
  display: flex;
  flex-wrap: wrap;
}
.numbers button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--darkcolor);
}
